import "./style/global.css";
import "./style/strucutres.css";
import "./style/fontawesome.css";

import favicon16 from "../img/favicon/favicon16.png"
import favicon32 from "../img/favicon/favicon32.png"
import favicon48 from "../img/favicon/favicon48.png"

import React from "react";
import { StaticQuery, Link, graphql } from "gatsby";
import { Main, Main2, Aside, Logo, Navigator, LinksList, SocialList } from "./style/emo-layout.js";
import { Helmet } from "react-helmet"
import Marquee from "react-fast-marquee";



const Layout = ({ children }) => (
	<StaticQuery
	query = { 
		graphql`
		query {
			site {
				siteMetadata {
					title
					subtitle
					description
					keywords
				}
			}
		}
		`}
		render = { data => (
			<React.Fragment>
			<div class = "banner"> <h3>{data.site.siteMetadata.title}</h3> </div>
			<Marquee gradient = {false} speed = {30} pauseOnHover = {true} ><div class = "marquee"> <h4> INVESTIGATING THE METHODS AND GEOGRAPHIES CONTEMPORARY REAL ESTATE SPECULATION  </h4></div></Marquee>
			<Helmet
			
			meta = {[
				{
					name: "description",
					content: data.site.siteMetadata.description,
				},
				{
					name: "keywords",
					content: data.site.siteMetadata.keywords,
				},
				]}
				link={[
					{ rel: 'icon', type: 'image/png', sizes: "16x16", href: `${favicon16}` },
					{ rel: 'icon', type: 'image/png', sizes: "32x32", href: `${favicon32}` },
					{ rel: 'shortcut icon', type: 'image/png', href: `${favicon48}` },
					]}
					/>
					<Aside>
						<Navigator>
							
							<nav>
								<LinksList>
									<li><Link to={`/`} activeClassName="active">All</Link></li>
									<li><Link to={`/tag-background/`} activeClassName="active">Background</Link></li>
									<li><Link to={`/tag-growing-investors/`} activeClassName="active">Growing Investors</Link></li>
									<li><Link to={`/tag-inst-flipping/`} activeClassName="active">Institutionalized Flipping</Link></li>
									<li><Link to={`/tag-div-geographies/`} activeClassName="active">Diverging Geographies</Link></li>
									<li>Sites:</li>
									<li><Link to={`/tag-site-philly/`} activeClassName="active">Philadelphia</Link></li>
									<li><Link to={`/tag-site-buffalo/`} activeClassName="active">Buffalo</Link></li>
									<li><Link to={`/tag-site-newark/`} activeClassName="active">Newark</Link></li>
								</LinksList>
							</nav>
						</Navigator>
					</Aside>
					<Main>
						<div class = "overarching-grid">
								{children}</div>
						</Main>
					</React.Fragment>
					)}
		/>
		);

export default Layout;
